.navbar {
    background-color: #FFFFFF;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background:none;
}

.nav-menu {
    background-color: #FFFFFF;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left:0;
    transition: 350ms;
    margin-right: 10px;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration:none;
    color: #01575C;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 8px;
    border-radius: 4px;
}

.nav-text a:hover {
    color: #59BA89;
}

.nav-menu-items {
    width:100%;
    margin-right: 38px;
}

.navbar-toggle {
    background-color: #FFFFFF;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items:center;
}

span {
    margin-left:16px;
}

#logo {
    margin-top: 60px;
    margin-left: 54px;
}

.logo-text {
    margin-left: 45px;
    font-size: 1.5rem;
    font-weight: bold;
    color: #01575C;
  }